.header {
  background-color: #ffb8ca;
}

.header__container {
  align-items: center;
  padding-left: 20px;
  padding-right: 20px;
  display: flex;
  position: relative;
}

.header__logo {
  margin-top: 8px;
  margin-bottom: 8px;
}

.header__menu-open {
  background-color: rgba(0, 0, 0, 0);
  border: 0;
  margin-left: auto;
  padding: 0;
}

.header__menu-icon {
  stroke: #d41443;
  transition: stroke .25s cubic-bezier(.4, 0, .2, 1);
}

.header__menu-open:hover .header__menu-icon, .header__menu-open:focus .header__menu-icon {
  stroke: #fff;
}

.header__nav {
  display: none;
}

.header__nav.is-open {
  z-index: 15;
  height: 100vh;
  width: 100vw;
  background-color: #e17992;
  flex-direction: column;
  padding-top: 20px;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  overflow-y: scroll;
}

.header__menu-close {
  background-color: rgba(0, 0, 0, 0);
  border: 0;
  margin-bottom: 26px;
  margin-left: auto;
  margin-right: 20px;
  padding: 0;
}

.header__close-icon {
  stroke: #fff;
  transition: stroke .25s cubic-bezier(.4, 0, .2, 1);
}

.header__menu-close:hover .header__close-icon, .header__menu-close:focus .header__close-icon {
  stroke: #d41443;
}

.header__modal-button {
  width: 209px;
  color: #d41443;
  background-color: #fff;
  border: 0;
  border-radius: 22px;
  justify-content: center;
  align-self: center;
  align-items: baseline;
  margin-bottom: 19px;
  padding: 11.5px 0;
  font-family: DM Sans, sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 1.31;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.header__modal-button:hover, .header__modal-button:focus {
  background-color: #e1e1e1;
}

.header__arrow-icon {
  stroke: #d41443;
  margin-left: 21px;
}

.header__nav-item {
  border-top: 1px solid #e18298;
  padding: 13px 20px;
}

.header__nav-item:last-child {
  border-bottom: 1px solid #e18298;
}

.header__nav-link {
  color: #fff;
  font-family: DM Sans, sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: 1.28;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

